import Vue from 'vue'
import App from './app/index.vue'
import router from './router'
import store from './store'
import i18n from './i18n'
import {BootstrapVue, BIconEnvelope, BIconPhone, BIconMap, BIconBarChart} from 'bootstrap-vue'

import './assets/css/styles.scss'

import { library } from '@fortawesome/fontawesome-svg-core'
import { faFacebook, faTwitter, faYoutube } from '@fortawesome/free-brands-svg-icons'

import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome'
import Chart from "chart.js";

library.add(faFacebook, faTwitter)
Vue.component('fa-icon', FontAwesomeIcon)

Vue.use(BootstrapVue)
Vue.component('BIconEnvelope', BIconEnvelope)
Vue.component('BIconPhone', BIconPhone)
Vue.component('BIconBarChartFill', BIconBarChart)
Vue.component('BIconMap', BIconMap)

Vue.config.productionTip = false

Chart.defaults.global.defaultFontSize = 14

class Application {
  constructor() {
    this.run()
  }

  run() {
    new Vue({
      router,
      store,
      i18n,
      render: (h) => h(App),
    }).$mount('#app')
  }
}

export default new Application()
