import {ChartTooltipItem} from "chart.js";

export enum ChartType {
    BAR, LINE
}

//ChartOptions
export function getChartOptions(title: string, yLabel: string, stacked: boolean = false, digitsNum: number = 0) {
    let options = {
        plugins: {
            datalabels: {
                textAlign: "center",
                padding: 1,
                align: 'top',
                anchor: 'end',
                borderColor: '#999999',
                backgroundColor: '#EEEEEE',
                borderWidth: 1,
                borderRadius: 3,
                font: {
                    size: 12
                },
                formatter: function (value: number) {
                    return nFormatter(value, digitsNum)
                }
            }
        },
        tooltips: {
            enabled: true,
            callbacks: {
                label: function (item: ChartTooltipItem) {
                    if (item.value !== undefined) {
                        return Number(item.value).toLocaleString();
                    }
                    return item.value
                }
            }
        },
        responsive: true,
        maintainAspectRatio: false,
        scales: {
            yAxes: [{
                ticks: {
                    beginAtZero: true,
                    callback: function (value: number) {
                        return nFormatter(value, digitsNum)
                    }
                },
                stacked: stacked,
                scaleLabel: {
                    display: true,
                    labelString: yLabel.toLowerCase()
                }
            }]
        },
        legend: {
            display: true,
            position: "bottom"
        },
        title: {
            display: true,
            text: title.toUpperCase()
        }
    }

    return options
}

function nFormatter(num: number, digitsNum: number = 0): string {
    if (num >= 1000000) {
        return (num / 1000000).toFixed(digitsNum).replace(/\.0$/, '') + 'M';
    }
    if (num >= 1000) {
        return (num / 1000).toFixed(digitsNum).replace(/\.0$/, '') + 'K';
    }
    return num.toString();
}

export function getBarDataset(label: string, color: string, values: number[]) {
    return {
        label: label,
        backgroundColor: color,
        data: values
    }
}

export function getLineDataset(label: string, color: string, values: number[]) {
    return {
        label: label,
        fill: false,
        borderColor: color,
        backgroundColor: color,
        pointRadius: 6,
        data: values
    }
}

