import Component from 'vue-class-component'
import {Vue, Watch} from 'vue-property-decorator'

import routes from '@/router/routes'

import Footer from "@/app/footer/index.vue";


interface RouteInterface {
  name: string
  path?: string
  redirect?: string
  meta?: {
    show: boolean
  }
  component?: any
}

@Component({
  components: {
    'v-footer': Footer
  },
})
export default class App extends Vue {
  mount() {
    document.title = String(this.$i18n.t('main.portal'))
  }

  private setLocale(locale: string) {
    this.$i18n.locale = locale
    document.title = String(this.$i18n.t('main.portal'))
  }

  private isCurrentLocale(locale: string): boolean {
    return this.$i18n.locale === locale
  }


  private get routes(): RouteInterface[] {
    return routes.filter((route) => {
      return route.meta ? route.meta.show !== false : true
    })
  }

  private get version(): string {
    return process.env.VUE_APP_GIT_VERSION || 'undefined'
  }

}
