import Component from 'vue-class-component'
import {Vue} from 'vue-property-decorator'
import LineChart from "@/components/charts/LineChart"
import {CONFIG, Data, fetchConfig, IndicatorData, IndicatorType} from "@/shared/data";
import {DEFAULT_MONTH, REPUBLIC_CODE} from "@/shared/—Åonstants";
import {ChartType, getChartOptions, getLineDataset} from "@/shared/charts";

@Component({
    components: {
        'v-line-indicator': LineChart
    }
})
export default class Indicator extends Vue {
    private preparingData1 = !Indicator.isReady(IndicatorType.CRIMINAL)
    private preparingData2 = !Indicator.isReady(IndicatorType.ADMINISTRATIVE)
    private preparingData3 = !Indicator.isReady(IndicatorType.ACCIDENT)
    private preparingData4 = !Indicator.isReady(IndicatorType.CIVIL)
    private preparingData5 = !Indicator.isReady(IndicatorType.ERSOP)

    public data1: IndicatorData = {divider: 1, labels: [], values: []}
    public data2: IndicatorData = {divider: 1, labels: [], values: []}
    public data3: IndicatorData = {divider: 1, labels: [], values: []}
    public data4: IndicatorData = {divider: 1, labels: [], values: []}
    public data5: IndicatorData = {divider: 1, labels: [], values: []}

    private static isReady(type: IndicatorType) {
        return Data.isMonthFetched(type, Indicator.getCurrentMonth(type))
    }

    private static getCurrentMonth(type: IndicatorType) {
        let month = DEFAULT_MONTH

        if (type === IndicatorType.ACCIDENT && CONFIG !== undefined) {
            month = CONFIG.accident.month
        }
        if (type === IndicatorType.ADMINISTRATIVE && CONFIG !== undefined) {
            month = CONFIG.administrative.month
        }
        if (type === IndicatorType.CIVIL && CONFIG !== undefined) {
            month = CONFIG.civil.month
        }
        if (type === IndicatorType.CRIMINAL && CONFIG !== undefined) {
            month = CONFIG.criminal.month
        }
        if (type === IndicatorType.ERSOP && CONFIG !== undefined) {
            month = CONFIG.ersop.month
        }
        return month
    }

    async mounted() {
        await fetchConfig()

        if (!Indicator.isReady(IndicatorType.CRIMINAL)) {
            this.preparingData1 = true
            // TODO –µ—Å–ª–∏ –Ω–µ –ø–µ—Ä–µ–¥–∞—ë—Ç—Å—è —Ç–µ–∫—É—â–∏–π –º–µ—Å—è—Ü, –±—Ä–∞—Ç—å –¥–µ—Ñ–æ–ª—Ç–Ω—ã–π –¥–ª—è —Ä–∞–∑–¥–µ–ª–∞ –∏–∑ –∫–æ–Ω—Ñ–∏–≥–∞
            Data.fetchData(IndicatorType.CRIMINAL, Indicator.getCurrentMonth(IndicatorType.CRIMINAL))
                .then(() => {
                    this.data1 = Data.getYearsData(IndicatorType.CRIMINAL, 1, REPUBLIC_CODE, Indicator.getCurrentMonth(IndicatorType.CRIMINAL))
                })
                .finally(() => this.preparingData1 = false)
        } else {
            this.data1 = Data.getYearsData(IndicatorType.CRIMINAL, 1, REPUBLIC_CODE, Indicator.getCurrentMonth(IndicatorType.CRIMINAL))
        }

        if (!Indicator.isReady(IndicatorType.ADMINISTRATIVE)) {
            this.preparingData2 = true
            Data.fetchData(IndicatorType.ADMINISTRATIVE, Indicator.getCurrentMonth(IndicatorType.ADMINISTRATIVE))
                .then(() => {
                    this.data2 = Data.getYearsData(IndicatorType.ADMINISTRATIVE, 1, REPUBLIC_CODE, Indicator.getCurrentMonth(IndicatorType.ADMINISTRATIVE))
                })
                .finally(() => this.preparingData2 = false)
        } else {
            this.data2 = Data.getYearsData(IndicatorType.ADMINISTRATIVE, 1, REPUBLIC_CODE, Indicator.getCurrentMonth(IndicatorType.ADMINISTRATIVE))
        }

        if (!Indicator.isReady(IndicatorType.ACCIDENT)) {
            this.preparingData3 = true
            Data.fetchData(IndicatorType.ACCIDENT, Indicator.getCurrentMonth(IndicatorType.ACCIDENT))
                .then(() => {
                    this.data3 = Data.getYearsData(IndicatorType.ACCIDENT, 7, REPUBLIC_CODE, Indicator.getCurrentMonth(IndicatorType.ACCIDENT))
                })
                .finally(() => this.preparingData3 = false)
        } else {
            this.data3 = Data.getYearsData(IndicatorType.ACCIDENT, 7, REPUBLIC_CODE, Indicator.getCurrentMonth(IndicatorType.ACCIDENT))
        }

        if (!Indicator.isReady(IndicatorType.CIVIL)) {
            this.preparingData4 = true
            Data.fetchData(IndicatorType.CIVIL, Indicator.getCurrentMonth(IndicatorType.CIVIL))
                .then(() => {
                    this.data4 = Data.getYearsData(IndicatorType.CIVIL, 1, REPUBLIC_CODE, Indicator.getCurrentMonth(IndicatorType.CIVIL))
                })
                .finally(() => this.preparingData4 = false)
        } else {
            this.data4 = Data.getYearsData(IndicatorType.CIVIL, 1, REPUBLIC_CODE, Indicator.getCurrentMonth(IndicatorType.CIVIL))
        }

        if (!Indicator.isReady(IndicatorType.ERSOP)) {
            this.preparingData5 = true
            Data.fetchData(IndicatorType.ERSOP,  Indicator.getCurrentMonth(IndicatorType.ERSOP))
                .then(() => {
                    this.data5 = Data.getYearsData(IndicatorType.ERSOP, 1, REPUBLIC_CODE,  Indicator.getCurrentMonth(IndicatorType.ERSOP))
                })
                .finally(() => this.preparingData5 = false)
        } else {
            this.data5 = Data.getYearsData(IndicatorType.ERSOP, 1, REPUBLIC_CODE,  Indicator.getCurrentMonth(IndicatorType.ERSOP))
        }
    }

    get chart1() {
        return {
            type: ChartType.LINE,
            data: {
                labels: this.data1.labels,
                datasets: [
                    getLineDataset(this.$t('xLabels.total').toString(), '#2e86c1', this.data1.values)
                ]
            },
            options: getChartOptions(this.$t('indicators.criminal-header').toString(), this.$t('yLabels.quantity').toString())
        }
    }

    get chart2() {
        return {
            type: ChartType.LINE,
            data: {
                labels: this.data2.labels,
                datasets: [
                    getLineDataset(this.$t('xLabels.total').toString(), '#2e86c1', this.data2.values)
                ]
            },
            options: getChartOptions(this.$t('xLabels.administrative.registered').toString(), this.$t('yLabels.quantity').toString(), false, 2)
        }
    }

    get chart3() {
        return {
            type: ChartType.LINE,
            data: {
                labels: this.data3.labels,
                datasets: [
                    getLineDataset(this.$t('xLabels.total').toString(), '#2e86c1', this.data3.values)
                ]
            },
            options: getChartOptions(this.$t('xLabels.accident.registered').toString(), this.$t('yLabels.quantity').toString())
        }
    }

    get chart4() {
        return {
            type: ChartType.LINE,
            data: {
                labels: this.data4.labels,
                datasets: [
                    getLineDataset(this.$t('xLabels.total').toString(), '#2e86c1', this.data4.values)
                ]
            },
            options: getChartOptions(this.$t('xLabels.civil.received').toString(), this.$t('yLabels.quantity').toString())
        }
    }

    get chart5() {
        return {
            type: ChartType.LINE,
            data: {
                labels: this.data5.labels,
                datasets: [
                    getLineDataset(this.$t('xLabels.total').toString(), '#2e86c1', this.data5.values)
                ]
            },
            options: getChartOptions(this.$t('indicators.ersop').toString(), this.$t('yLabels.quantity').toString())
        }
    }

}
