import Papa from 'papaparse'
import {REGIONS, REPUBLIC_CODE} from "@/shared/—Åonstants";

export let CONFIG!: AppConfig
export let YEARS = ['2015', '2016', '2017', '2018', '2019', '2020']

export interface ModuleConfig {
    year: string
    month: number
}

export interface AppConfig {
    years: string[]
    accident: ModuleConfig
    "administrative": ModuleConfig
    "civil": ModuleConfig
    "criminal": ModuleConfig
    "ersop": ModuleConfig
}

export interface IndicatorValue {
    id: number
    year: string
    area: string
    value: number
}

export interface IndicatorData {
    indicator?: number
    divider: number
    labels: string[]
    values: number[]
}

export enum IndicatorType {
    CRIMINAL,
    ADMINISTRATIVE,
    ACCIDENT,
    CIVIL,
    ERSOP
}

export async function fetchConfig() {
    if (CONFIG === undefined) {
        let loadedConfig = await fetch(`/data/data.json`).then((response) => response.json())
        if (loadedConfig !== undefined) {
            CONFIG = loadedConfig
            if (loadedConfig.years !== undefined) {
                YEARS = loadedConfig.years
            }
        }
    }
}

export async function getData(type: IndicatorType, month: number) {
    await fetchConfig()

    let typePath
    if (type === IndicatorType.CRIMINAL) {
        typePath = 'criminal'
    }
    if (type === IndicatorType.ADMINISTRATIVE) {
        typePath = 'administrative'
    }
    if (type === IndicatorType.ACCIDENT) {
        typePath = 'accident'
    }
    if (type === IndicatorType.CIVIL) {
        typePath = 'civil'
    }
    if (type === IndicatorType.ERSOP) {
        typePath = 'ersop'
    }
    if (typeof typePath === 'undefined') {
        return
    }
    return await fetch(`/data/${typePath}/m${month}.csv`)
        .then((response) => response.text())
        .then((value: string) => Papa.parse<any>(value))
        .then((result: any) => result.data.slice(1))
}

export class Data {
    private static crimeValues: Map<number, IndicatorValue[]> = new Map();
    private static administrativeValues: Map<number, IndicatorValue[]> = new Map();
    private static accidentValues: Map<number, IndicatorValue[]> = new Map();
    private static civilValues: Map<number, IndicatorValue[]> = new Map();
    private static ersopValues: Map<number, IndicatorValue[]> = new Map();

    private static parse(type: IndicatorType, data: any): IndicatorValue[] {
        if (typeof data === 'undefined') {
            return []
        }
        const values: IndicatorValue[] = []

        const republic: IndicatorValue[] = []
        const regions: IndicatorValue[] = []
        const transportRegion: IndicatorValue[] = []

        data.forEach((item: any) => {
            const value: IndicatorValue = {
                id: Number(item[2]),
                year: String(item[1]),
                area: String(item[3]),
                value: Number(item[4])
            }
            values.push(value)

            if (type === IndicatorType.CRIMINAL || type === IndicatorType.ACCIDENT) {
                // —Ä–∞—Å—Å—á–∏—Ç—ã–≤–∞–µ–º –¥–∞–Ω–Ω—ã–µ –ø–æ –†–µ—Å–ø—É–±–ª–∏–∫–µ
                const republicFound = republic.find(republicValue => republicValue.id === value.id && republicValue.year === value.year && republicValue.area === value.area.substr(0, 2))
                if (republicFound) {
                    republicFound.value = republicFound.value + value.value
                } else {
                    const republicValue: IndicatorValue = {
                        id: value.id,
                        year: value.year,
                        area: value.area.substr(0, 2),
                        value: value.value
                    }
                    republic.push(republicValue)
                }
            }

            if (type === IndicatorType.CRIMINAL || type === IndicatorType.ACCIDENT) {
                // —Ä–∞—Å—Å—á–∏—Ç—ã–≤–∞–µ–º –¥–∞–Ω–Ω—ã–µ –ø–æ —Ä–µ–≥–∏–æ–Ω–∞–º
                const regionFound = regions.find(regionValue => regionValue.id === value.id && regionValue.year === value.year && regionValue.area === value.area.substr(0, 4))
                if (regionFound) {
                    regionFound.value = regionFound.value + value.value
                } else {
                    const regionValue: IndicatorValue = {
                        id: value.id,
                        year: value.year,
                        area: value.area.substr(0, 4),
                        value: value.value
                    }
                    regions.push(regionValue)
                }
            }

            // —Ä–∞—Å—Å—á–∏—Ç—ã–≤–∞–µ–º –¥–∞–Ω–Ω—ã–µ –ø–æ —Ç—Ä–∞–Ω—Å–ø–æ—Ä—Ç–Ω–æ–º—É —Ä–µ–≥–∏–æ–Ω—É
            if (['1974', '1977', '1978'].includes(value.area.substr(0, 4))) {
                const transportRegionFound = transportRegion.find(regionValue => regionValue.id === value.id && regionValue.year === value.year)
                if (transportRegionFound) {
                    transportRegionFound.value = transportRegionFound.value + value.value
                } else {
                    const transportRegionValue: IndicatorValue = {
                        id: value.id,
                        year: value.year,
                        area: '1970',
                        value: value.value
                    }
                    transportRegion.push(transportRegionValue)
                }
            }
        })

        values.push(...transportRegion)
        values.push(...regions)
        values.push(...republic)

        return values
    }

    public static async fetchData(type: IndicatorType, month: number) {
        if (!this.isMonthFetched(type, month)) {
            let data = await getData(type, month)

            if (type === IndicatorType.CRIMINAL) {
                this.crimeValues.set(month, this.parse(type, data))
            }
            if (type === IndicatorType.ADMINISTRATIVE) {
                this.administrativeValues.set(month, this.parse(type, data))
            }
            if (type === IndicatorType.ACCIDENT) {
                this.accidentValues.set(month, this.parse(type, data))
            }
            if (type === IndicatorType.CIVIL) {
                this.civilValues.set(month, this.parse(type, data))
            }
            if (type === IndicatorType.ERSOP) {
                this.ersopValues.set(month, this.parse(type, data))
            }
        }
    }

    public static isMonthFetched(type: IndicatorType, month: number): boolean {
        if (type === IndicatorType.CRIMINAL) {
            return this.crimeValues.has(month)
        }
        if (type === IndicatorType.ADMINISTRATIVE) {
            return this.administrativeValues.has(month)
        }
        if (type === IndicatorType.ACCIDENT) {
            return this.accidentValues.has(month)
        }
        if (type === IndicatorType.CIVIL) {
            return this.civilValues.has(month)
        }
        if (type === IndicatorType.ERSOP) {
            return this.ersopValues.has(month)
        }
        return false
    }

    public static getYearsData(type: IndicatorType, indicator: number, area: string, month: number): IndicatorData {
        const data: IndicatorData = {divider: 1, indicator: indicator, labels: [], values: []}

        let values = this.getValues(type, month)

        if (typeof values == 'undefined') {
            return data
        }

        const items = values.filter(item => (item.id === indicator && item.area === area))

        YEARS.map(year => {
            const yearItem = items.find(item => item.year === year)
            if (yearItem) {
                data.labels.push(year)
                data.values.push(yearItem.value)
            } else {
                if (year !== YEARS[YEARS.length - 1]) {
                    data.labels.push(year)
                    data.values.push(0)
                }
            }
        })

        // const minValue = Math.min(...data.values)
        // if (minValue < 1000) {
        //     data.divider = 1
        // } else if (minValue >= 1000 && minValue < 1000000) {
        //     data.divider = 1000
        // } else if (minValue >= 1000000 && minValue < 1000000000) {
        //     data.divider = 1000000
        // } else {
        //     data.divider = 1000000000
        // }

        return data
    }

    public static getAreasData(type: IndicatorType, indicator: number, year: string, parentArea: string, month: number): IndicatorValue[] {
        let values = this.getValues(type, month)

        if (typeof values == 'undefined') {
            return []
        }

        if (parentArea.length === 6) {
            return values.filter(item => (item.id === indicator && item.year === year && item.area === parentArea))
        } else if (parentArea.length === 4) {
            return values.filter(item => (item.id === indicator && item.year === year && (item.area.substring(0, 4) === parentArea || item.area === parentArea)))
        } else if (parentArea.length === 2) {
            return values.filter(item => item.id === indicator && item.year === year && ((item.area.substring(0, 2) === parentArea && item.area.length === 4) || item.area === parentArea))
        }

        return []
    }

    public static getTableData(type: IndicatorType, indicator: number, month: number): any {
        let values = this.getValues(type, month)

        if (typeof values == 'undefined') {
            return []
        }

        let tableData = [REPUBLIC_CODE, ...REGIONS].map((region: string) => {
            let regionRow: any = {region: region}
            YEARS.map((year: string) => {
                regionRow[year] = 0
            })
            return regionRow
        })

        console.log(Object.assign({}, tableData))


        const indicatorValues = values.filter(item => item.id === indicator)

        console.log(indicatorValues)

        indicatorValues.forEach(item => {
            const tableRow = tableData.find(row => row.region === item.area)
            // const tableRow = tableData.find(row => row.region === item.area.substring(0, 4) || (row.region === item.area && row.region === REPUBLIC_CODE))
            if (tableRow !== undefined) {
                tableRow[item.year] = tableRow[item.year] + item.value
            }
        })

        return tableData
    }

    private static getValues(type: IndicatorType, month: number): IndicatorValue[] | undefined {
        if (type === IndicatorType.CRIMINAL) {
            return this.crimeValues.get(month) || []
        }
        if (type === IndicatorType.ADMINISTRATIVE) {
            return this.administrativeValues.get(month) || []
        }
        if (type === IndicatorType.ACCIDENT) {
            return this.accidentValues.get(month) || []
        }
        if (type === IndicatorType.CIVIL) {
            return this.civilValues.get(month) || []
        }
        if (type === IndicatorType.ERSOP) {
            return this.ersopValues.get(month) || []
        }

        return undefined
    }
}

export function downloadCSV(items: any) {
    const header: string[] = []
    header.push('regionTitle')
    YEARS.forEach(year => header.push(year))

    const replacer = (key: any, value: any) => value === null ? '' : value // specify how you want to handle null values here

    let csv = items.map((row: any) => header.map(fieldName => JSON.stringify(row[fieldName], replacer)).join(','))
    csv.unshift(header.join(','))

    let csvContent = "data:text/csv;charset=utf-8," + csv.join('\r\n');
    const encodedUri = encodeURI(csvContent);
    const link = document.createElement("a");
    link.setAttribute("href", encodedUri);
    link.setAttribute("download", "data.csv");
    document.body.appendChild(link);
    link.click();
}
